
@import url("https://fonts.googleapis.com/css?family=Open+Sans:600,700|PT+Serif");
.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3em;
}
.page .page-body {
    width: 60%;
    text-align: left;
}
.page .page-body ul {
      list-style: none;
}
.page .page-body ul li {
        margin: 0em;
}
.page .page-body a {
      text-decoration: none;
}
.page .page-body a:hover {
        text-decoration: underline;
        color: #000;
}
.content h2 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 2.5rem;
  color: #3e70c1;
  margin: .4em 0em;
}
.content h3 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 1.5rem;
  color: #000;
  margin: .2em 0em;
}
.content p {
  white-space: pre-wrap;
  font-family: 'PT Serif', serif;
  font-size: 1.15rem;
  margin: .2em 0em;
}



