
@import url("https://fonts.googleapis.com/css?family=Open+Sans:600,700|PT+Serif");
.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3em;
}
.page .blog-post {
    width: 60%;
    text-align: left;
    margin-bottom: 5em;
}
.page .blog-post .header {
      display: flex;
      align-items: flex-start;
      flex-direction: row;
}
.page .blog-post .header .headshot {
        display: inline-block;
        height: 200px;
}
.page .blog-post .header .info {
        margin-left: 2em;
}
.page .blog-post .header .info h3 {
          margin: .2em 0em;
}
.page .blog-post .header .info p {
          margin: .1em 0em;
}
.content h2 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 2.5rem;
  color: #3e70c1;
  margin: .4em 0em;
}
.content p {
  white-space: pre-wrap;
  font-family: 'PT Serif', serif;
  font-size: 1.15rem;
}



